import {
  Button, Spinner, FormControl, Container, Input,
  FormLabel, UnorderedList, ListItem, Box, Image, Heading,
} from '@chakra-ui/react'
import { useQuery, gql } from '@apollo/client'
import React, { useEffect, useState } from 'react'
import { useParams } from 'react-router'
import ReactMarkdown from 'react-markdown'
import { httpURL } from '../helpers'
import EditOrList from './EditOrList'
import { useLocation } from 'react-router-dom'

const TOKEN = gql(`
  query GetToken($id: String!) {
    token(id: $id) {
      identifier
      URI
    }
  }
`)

const useQueryParams = () => (
  new URLSearchParams(useLocation().search)
)

export default ({ contract, validNetwork }) => {
  const [metadata, setMetadata] = useState()
  const [newMetadata, setNewMetadata] = useState('')
  const [tokenId, setTokenId] = useState()
  const homepage = metadata?.properties?.external_url
  const wearables = metadata?.properties?.wearables ?? {}
  const query = useQueryParams()
  const params = useParams()
  let id = params.id
  let { loading, error, data } = useQuery(
    TOKEN, { variables: { id } },
  )

  useEffect(() => {
    if(data?.token) {
      setTokenId(data.token.identifier)

      ;(async () => {
        const res = await fetch(httpURL(data.token.URI))
        if(res.ok) {
          try {
            const metadata = await res.json()
            setMetadata(metadata)
          } catch(err) { // invalid JSON
            setMetadata(null)
          }
        } else {
          console.error('Metadata Response', res)
        }
      })()
    }
  }, [data?.token])

  if(id === undefined) {
    return (
      <EditOrList {...{ contract, validNetwork }}/>
    )
  }

  if(metadata === undefined || loading) {
    return (
      <Box align="center" mt={10}>
        <Spinner size="lg"/>
      </Box>
    )
  }

  const overwrite = async (evt) => {
    evt.preventDefault()
    const res = await fetch(httpURL(newMetadata))
    const metadata = res.json()
    await contract.setURI(newMetadata, tokenId)
    setMetadata(metadata)
  }

  if(metadata === null || query.get('overwrite')) {
    return (
      <Container as="form" onSubmit={overwrite}>
        <FormControl isRequired>
          <FormLabel>New Metadata</FormLabel>
          <Input
            value={newMetadata}
            onChange={(evt) => (
              setNewMetadata(evt.target.value)
            )}
            placeholder="Metadata is corrupt. Provide a replacement."
          />
        </FormControl>
        <Button
          type="submit" isDisabled={!validNetwork} mt={5}
          title={validNetwork ? 'Replace Metadata' : 'Connect to the correct network.'}
        >
          Overwrite Metadata
        </Button>
      </Container>
    )
  }

  return (
    <Container sx={{ a: { textDecoration: 'underline' } }}>
      <Heading size="md" my={5}>Name: {metadata.name}</Heading>
      <UnorderedList>
        <ListItem>Description:
          <Box ml={5}>
            <ReactMarkdown>
              {metadata.description}
            </ReactMarkdown>
          </Box>
        </ListItem>
        <ListItem>Homepage:{' '}
          {homepage ? <a href={homepage}>{homepage}</a> : <em>None</em>}
        </ListItem>
        <ListItem>Image:
          <Image src={httpURL(metadata.image)} maxH="15em"/>
        </ListItem>
        <ListItem>Models:{' '}
          {Object.keys(wearables).length === 0 ? (
            <em>None</em>
          ) : (
            <UnorderedList>
              {Object.entries(wearables).map(([mimetype, model]) => (
                <ListItem><a href={httpURL(model)}>{mimetype}</a></ListItem>
              ))}
            </UnorderedList>
          )}
        </ListItem>
      </UnorderedList>
    </Container>
  )
}